.footer {
    background-color: black;
    color: white;
    padding-top: 50px;
    width: 100%;
    height: 200px;
}
.footer a {
    text-decoration: none;
    border-bottom: solid 1px #ffbe07;
    color: #7b70eb;
}
@media screen and (max-width: 700px) {
    .footer {
        width: 100%;
    }
}


